import Vue from 'vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faSearch,
  faSignOutAlt,
  faSignInAlt,
  faLink,
  faBold,
  faUnderline,
  faItalic,
  faCode,
  faFileCode,
  faParagraph,
  faListUl,
  faListOl,
  faQuoteLeft,
  faWindowMinimize,
  faUndo,
  faRedo,
  faUser,
  faFileAlt,
  faAngleRight,
  faCogs,
  faBars } from '@fortawesome/free-solid-svg-icons'

/*           . */
library.add(faCogs, faAngleRight, faFileAlt, faBars, faUser, faSearch, faSignOutAlt, faSignInAlt, faLink, faBold, faUnderline, faItalic, faCode, faFileCode, faParagraph, faListOl, faListUl, faQuoteLeft, faWindowMinimize, faUndo, faRedo)

/*     */
Vue.component('f-icon', FontAwesomeIcon)



